.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.information {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin: 20px;
}

input {
  width: 200px;
  height: 30px;
  font-size: 20px;
  padding-left: 10px;
  margin: 5px;
}

button {
  width: 200px;
  height: 50px;
  margin-top: 15px;
}

.information button:hover {
  cursor: pointer;
}

.employees {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin: 20px;
}

.employee {
  width: 600px;
  height: auto;
  border: 5px solid black;
  margin: 10px;
  display: flex;
  padding: 10px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.employee h3 {
  margin: 10px;
}

.update {
  display: flex;
  flex-direction: column;
  padding: 10px;
  justify-content: center;
}

.delete {
  display: flex;
  flex-direction: column;
  padding: 10px;
  justify-content: center;
}
